/* Burger nav */
#burger {
  display: none;
  position: absolute;
  top: 25px;
  right: 25px;
  height: 20px;
  width: 35px;
  background: 0;
  border: 0;
  outline: none;
  z-index: 999;
}
#burger span {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 2px;
  width: 100%;
  background: white;
  transition: all .33s ease;
}
#burger:before,
#burger:after {
  content: 'after';
  text-indent: -999999px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  background: white;
  width: 100%;
  height: 2px;
  transition: all .33s 0.05s;
}
#burger:after {
  top: 0px;
}
#burger:before {
  bottom: 0px;
}
#burger.active span {
  transform: rotateY(90deg);
}
#burger.active:after {
  transform: translate3d(0, 9px, 0) rotate(45deg);
}
#burger.active:before {
  transform: translate3d(0, -9px, 0) rotate(-45deg);
}
